@font-face {
  font-family: "ProductSans";
  src: url("./fonts/Product\ Sans\ Regular.otf");
}

body {
  margin: 0;
  font-family: "ProductSans";
  letter-spacing: 1px;
  background-color: black;
}

#main-view {
  display: flex;
  justify-content: center;
  align-items: center;
}

section {
  margin: 2rem;
  background: white;
  border-radius: 10px;
  padding: 10px;
  box-shadow: 0 0 1em white;
}

.waifu-art {
  height: 40vh;
  object-fit: cover;
}

.contact {
  display: flex;
  align-items: center;
  gap: 1em;
  border-radius: 10px;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}
